* {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
}

body {
    font-family: 'Poppins', sans-serif;
}

.top-contact {
    width: 100%;
    height: 6vh;
    background-color: rgb(255, 251, 245);
    display: flex;
    justify-content: space-around;
    align-items: center;
}

.top-contact .top-links ul {
    display: flex;
    justify-content: center;
    gap: 30px;
    list-style: none;
}

.top-links ul li a {
    text-decoration: none;
    color: gray;
}

.top-links ul li a i {
    font-size: 18px;
}

.line p{
    font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
    font-weight: 600;
    color: gray;
}

.line p a {
    color: rgb(25, 195, 203);
    font-family: sans-serif;
    font-weight: 700 !important;
    text-decoration: none;
}

.call address {
    display: flex;
    justify-content: space-between;
    gap: 20px;
}

.call address a {
    font-size: 14px;
    text-decoration: none;
    font-style: normal;
    color: black;
}

/* top contact media  */

@media (max-width:1000px) {
    .top-contact .top-links ul{
        gap: 20px;
    }
    .line p{
        font-size: 14.5px ;
    }
}

@media (max-width:500px) {
    .line p{
        display: none;
    }

    .top-contact{
        justify-content: center;
    }
}



@media (max-width:920px) {
    .top-links{
        display: none;
    }

    .top-contact{
        gap: 15px;
        padding: 0 15px;
    }
}

/* navlinks  */
header {
    background-color: #fff;
    box-shadow: 0 4px 8px rgba(0, 0, 0, 0.2);
    display: flex;
    justify-content: space-around;
    align-items: center;
    height: 13vh;
}

.logo #hpage {
    display: flex !important;
    justify-content: center;
    align-items: center;
    gap: 10px;
    text-decoration: none !important;
}

.logo #hpage h2 {
    color: black;
}



.logo {
    display: flex;
    align-items: center;
    gap: 30px;
    justify-content: center;
}

header nav ul {
    display: flex;
    align-items: center;
    gap: 20px;
    list-style: none;
}

header nav ul li {
    display: flex;
    justify-content: center;
    align-items: center;
    padding: 10px 20px;
    transition: all 0.3s ease;
}

header nav ul li a {
    color: black;
    font-weight: 600;
    text-decoration: none;

}

header nav ul li:hover,
#active {
    background-color: rgb(110, 211, 250);
    color: #fff;
}

/* header nav ul li:active,header nav ul li:focus,header nav ul li:visited{
    background-color: red !important;
} */

header nav ul li a:hover {
    color: #fff;
}

.cart {
    display: flex;
    justify-content: center;
    align-items: center;
    gap: 20px;
}

.cart i:nth-child(3) {
    background-color: rgb(176, 176, 176);
    padding: 10px;
    border-radius: 50%;
}

header form input:nth-child(1) {
    width: 15rem;
    height: 2.5rem;
    background-color: rgb(255, 255, 255);
    border: 1px solid rgb(203, 203, 203);
    border-radius: 10px 0px 0px 10px;
    padding: 0 10px;
    font-size: 13px;
}

header form input:nth-child(1):focus {
    outline: none;
    background-color: aliceblue;
    border: none;
    box-shadow: 0 0 5px rgba(0, 0, 0, 0.2);
    font-size: 15px;
}

header form input:nth-child(2) {
    width: 5rem;
    height: 2.5rem;
    border: none;
    background-color: rgb(29, 187, 132);
    color: #fff !important;
    font-size: 15px;
    font-weight: 600;
    border-radius: 0 10px 10px 0;
}

.cart {
    color: black;
    /* background-color: red; */
    display: flex;
    justify-content: center;
    align-items: center;
    gap: 15px;
}

.cart img {
    width: 25px !important;
}

.cart #user {
    background-color: rgb(194, 194, 194) !important;
    display: flex;
    justify-content: center;
    align-items: center;
    padding: 10px;
    border-radius: 50%;
    margin-left: 12px;
}

#cart-img {
    position: relative;

}

.cart sup {
    position: absolute;
    background-color: rgb(80, 193, 238);
    /* padding: 5px; */
    width: 20px;
    height: 20px;
    border-radius: 50%;
    left: 17px;
    top: -5px;
    box-sizing: border-box;
    color: #fff;
    display: flex;
    justify-content: center;
    align-items: center;
}

#check {
    display: none;
}

.checkbtn {
    display: none;
}



header {
   width: 100%;
   
   z-index: 1000;
}

.sticky{
    position: fixed;
    top: 0;
}


.logo{
    margin-right: 8rem;
}

.nav .top-nav{
   top: 13%;
}

/* media  */
@media (max-width:940px) {
    .checkbtn {
        display: block;
    }

    .nav ul {
        position: fixed;
        width: 40%;
        height: 100vh;
        background: #000000;
        top: 19%;
        left: -40%;
        text-align: center;
        transition: all .5s;
        flex-direction: column;
        padding: 4rem 0;
        z-index: 10000;
    }

    
    #check:checked~.nav ul {
        left: 0;
    }

    .nav ul li a {
        color: #fff;
    }

    header {
        justify-content: space-between;
        padding: 0 2rem;
    }
}

@media (max-width:620px) {

    .logo{
        margin-left: 10px;
    }
    .logo #hpage img{
        width: 65px;
    }

    .logo #hpage{
        gap: 5px;
    }

    .logo #hpage h2{
        font-size: 20px;
    }
     
    .cart #user{
        padding: 7px;
    }

    .cart #user img{
        width: 22px !important;
    }

    .cart img{
        width: 22px !important;
    }
}

@media (max-width:530px) {
    header{
        padding: 0 1rem;
    }

    .logo{
        margin: 0;
    }

}

@media (max-width:440px) {
    .logo #hpage h2{
        font-size: 18px;
    }

    .logo #hpage img{
        width: 50px;
    }
}
@media (max-width:400px) {

    .cart #user img{
        width: 20px !important;
    }

    .cart img{
        width: 20px !important;
    }
}

@media (max-width:370px) {
    .cart{
        gap: 10px;
    }
    .cart #user{
        margin-left: 20px;
    }

    .logo #hpage{
        gap: 0px;
    }
}




