.emt {
    width: 100%;
    min-height: 70vh;
    /* background-color: rgb(223, 245, 255); */
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
    gap: 18px;

}

.emt-img img {
    width: 15rem;
    height: 15rem;
    border-radius: 50%;
    object-fit: cover;
}

.emt p {
    color: gray;
    font-size: 22px;
}

.emt button {
    width: 13rem;
    height: 2.6rem;
    border: 2px solid rgb(0, 236, 185);
    font-size: 16.5px;
    background-color: transparent;
    border-radius: 5px;
    font-weight: 500;
    cursor: pointer;
}

.emt button:hover {
    background-color: rgb(0, 236, 185);
    color: rgb(255, 255, 255);

    transition: 0.2s;
}

/*card content */

.adc-prod {
    width: 100%;
    min-height: 100vh;
    background-color: rgba(43, 220, 255, 0.189);
    display: flex;
    justify-content: space-evenly;
    align-items: flex-start;
    padding: 2rem 0;
    margin-top: 2rem;
    flex-wrap: wrap;
    
}


.prod-card {
    display: flex;
    padding: 0 1rem;
    flex-direction: column;
    background-color: rgb(255, 255, 255);
    min-height: 25vh;
    width: 50rem;
    box-sizing: border-box;
    font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
    border-radius: 15px;
}

.location {
    display: flex;
    background-color: #fff;
    justify-content: space-between;
    align-items: center;
    padding: 10px 25px 10px 20px !important;
    padding: 15px;
    border-radius: 10px;
}

#dlv-sub {
    background-color: transparent;
    border: none;
    color: rgb(0, 195, 255);
    font-size: 17px;
    /* margin-left: 0.9rem; */
    width: 4rem;
    /* background-color: bisque; */
    margin: 0;
    padding: 0;

}

.location form {
    margin: 0;
}

.location h4 {
     font-family: Cambria, Cochin, Georgia, Times, 'Times New Roman', serif;
    color: rgb(78, 78, 78);
    
}

.location form #dlv-verify {
    width: 15rem !important;
    height: 2.5rem !important;
    border: 1px solid rgb(185, 185, 185) !important;
    border-radius: 5px !important;
    margin-left: 10px;
    font-size: 14px;
}

.prod-content {
    padding: 20px 50px;
}

.prodecard-content h5 {
    font-size: 18px;
    font-weight: 500;
    text-transform: capitalize;
}

.prodecard-content p {
    font-size: 16px;
    color: gray;
    font-weight: 400;
    margin-bottom: 5px;
}

.prodecard-content i{
    color: rgb(255, 213, 0);
}

.prodecard-content #quan {
    color: gray;
    margin-right: 20px;
}

.prodecard-content button {
    width: 40px;
    height: 30px;
    font-weight: 600;
    font-size: 20px;
    color: rgb(0, 191, 255);
    background-color: #fff;
    border: none;
    box-shadow: 5px 5px 10px rgba(95, 95, 95, 0.304);
    border-radius: 5px;
}

.prodecard-content #inc {
    width: 40px;
    height: 30px;
    background-color: rgb(231, 231, 231);
    box-shadow: none;
    border-radius: 0;
    color: rgb(31, 31, 31);
    font-size: 15px;
}

.prod-content {
    display: flex;
    gap: 30px;
}

.rs {
    font-size: 29px;
    /* margin-left: 30px; */
    
}

.rs del {
    font-size: 16px !important;
    margin-left: 5px;

}

.buy {
    width: 10rem !important;
    height: 2.6rem !important;
    background-color: rgb(49, 210, 255) !important;
    cursor: pointer;
    color: rgb(255, 255, 255) !important;
    font-size: 16px !important;
    font-family: 'Gill Sans', 'Gill Sans MT', Calibri, 'Trebuchet MS', sans-serif;
    margin-right: 4rem !important;
    margin-top: 20px !important;
}

.buy:hover {
    background-color: rgb(27, 194, 255) !important;

}

.remove {
    width: 10rem !important;
    height: 2.6rem !important;
    color: rgb(255, 255, 255) !important;
    font-size: 16px !important;
    font-family: 'Gill Sans', 'Gill Sans MT', Calibri, 'Trebuchet MS', sans-serif;
    margin-top: 20px !important;
    background-color: rgba(255, 0, 0, 0.4) !important;
    cursor: pointer;
}

.remove:hover {
    background-color: rgb(255, 18, 18) !important;
}


.prodcard-img img {
    width: 6rem !important;
    height: 6rem !important;
    object-fit: cover;
    margin-bottom: 22px;
}

.prod-list {
    display: flex;
    flex-direction: column;
    gap: 30px;
}

.prodcard-img #quan {
    color: gray;
    margin-right: 20px;
    font-size: 14px;
    background-color: aqua;
}

.prodcard-img #inc {
    width: 40px;
    height: 30px;
    background-color: rgb(231, 231, 231);
    box-shadow: none;
    border-radius: 0;
    color: rgb(31, 31, 31);
    font-size: 15px;
}
.prodcard-img button {
    width: 40px;
    height: 30px;
    font-weight: 600;
    font-size: 20px;
    color: rgb(0, 191, 255);
    background-color: #fff;
    border: none;
    box-shadow: 5px 5px 10px rgba(95, 95, 95, 0.304);
    border-radius: 5px;
}

.quantity1{
    display: none;
}
/*product total */

.prod-total {
    min-height: 50vh;
    width: 27rem;
}

.coupan {
    width: 27rem;
    background-color: #fff;
    height: 3.8rem;
    border-radius: 10px;
}

.total-amt {
    width: 27rem;
    min-height: 10rem;
    background-color: #ffffff;
    margin-top: 26px;
    border-radius: 10px;
}

.prod-total button {
    width: 27rem;
    height: 3rem;
    background-color: rgb(54, 195, 255);
    margin-top: 20px;
    border: none;
    color: #fff;
    border-radius: 5px;
    font-size: 17px;
    font-weight: 600;
    text-transform: capitalize;
    cursor: pointer;
}

.prod-total button:hover {
    background-color: rgb(0, 0, 0);
}

.coupan {
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
    padding: 5px 5px 8px 10px;
    gap: 10px;
}

.coupan form {
    margin-top: 0 !important;
}

.coupan #cup {
    width: 15rem;
    border-radius: 10px 0 0 10px;
    height: 2.6rem;
    border: none;
    border: 1.5px solid rgb(221, 221, 221);
    font-size: 15px;
    margin: 0 !important;
}

.coupan #apl {
    width: 6rem;
    border: none;
    height: 2.6rem;
    background-color: rgb(20, 203, 142);
    color: #fff;
    font-size: 14px;
    font-weight: 500;
    margin: 0;
    padding: 0;
    border-radius: 0 10px 10px 0;
    cursor: pointer;
    margin-top: 0.1rem;
}

.total-amt table {
    width: 27rem;
    /* background-color: rgb(239, 239, 239); */
    font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
    border-radius: 5px;
}

.total-amt table tr {
    height: 3rem;
}

.total-amt table tr th {
    text-align: left;
    padding: 0 0 0 20px;

}

.total-amt table tr td {
    text-align: right;
    padding: 0 20px 0 0;
}

#total {
    font-size: 25px;
}

#t-amt {
    background-color: rgb(234, 234, 234);
    /* outline: 1px solid rgb(182, 182, 182) !important; */
}

.total-amt table th span {
    font-weight: 500;
}

@media (max-width:1250px) {
    .prod-card {
        width: 45rem !important;
    }
}

@media (max-width:1200px) {
    .prod-card {
        width: 42rem !important;
    }

    .prod-total{
        width: 25rem;
        /* margin-right: 10px; */
    }

    .total-amt{
       width: 25rem;
    }
    .prod-total button{
        width: 25rem;
    }

    .total-amt table {
        width: 25rem;
    }

    .coupan{
        padding: 5px 0px 8px 0px;
        width: 25rem;
    }

}

@media (max-width:1100px) {
    .adc-prod{
        gap: 15px;
    }
}

@media (max-width:1087px) {
    .adc-prod{
        justify-content: center !important;
        /* background-color: aquamarine; */
    }
    .prod-list{
        width: 90% !important;
    }

    .prod-card{
        width: 100% !important;
    }

    .prod-total{
        width: 100%;
        background-color: #e0e0e0;
        display: flex;
        justify-content: flex-end;
        align-items: flex-end;
        flex-direction: column;
        padding: 10px 1rem;
        
    }

   .coupan{
    width: 100% !important;
    border-radius: 0;
    align-items: flex-end;
    padding: 0 1rem;
    background-color: transparent;
    background-image: linear-gradient(to right,transparent,rgb(255, 255, 255) 50%);
   }

}

@media (max-width:600px) {
    .location form #dlv-verify{
        width: 10rem !important;
    }
}

@media (max-width:565px) {
    .prod-content{
        padding: 20px 0px 20px 20px !important;
    }
}

@media (max-width:465px) {
    .prod-total{
        justify-content: center;
        align-items: center;
        background-color: #ffffff;
    }

    .coupan{
        width: initial !important;
        background-color: #ffffff;
        background-image: none;
        border-radius: 10px;
    }

    .total-amt{
        width: initial !important;
        
    }

    .rs{
        margin: 0;
        font-size: 22px;
    }

    .rs del{
        font-size: 14px !important;
    }

    .rsoff{
        font-size: 14px;
        margin-left: 2px;
    }

   .Quatity{
    display: none;
   }

   .quantity1{
    display: block;
}

}

@media (max-width:420px) {
    .prod-card{
        overflow: hidden;
    }

    .prod-total{
        overflow: hidden;
    }
    .total-amt{
        width: 100% !important;
        
    }
    .total-amt table{
        width: 100%;
        
    }
    
    
    #total{
        padding: 0 15px;
    }

    .prod-total button{
        width: 20rem !important;
    }

    .coupan #cup{
        width: 10rem;
    }

    .coupan #apl{
        width: 4rem;
        font-size: 12px;  
    }

    .prod-card{
        width: initial;
    }

    .location{
        flex-direction: column;
        justify-content: flex-start;
        align-items: flex-start;
        gap: 10px;
        padding: 10px 10px 10px 20px !important;
    }
    
    .location form #dlv-verify{
        margin: 0;
    }

    .location form #dlv-verify{
        width: 14rem !important;
    }


}

