* {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
}

.Bfooter {
    width: 100%;
    min-height: 60vh;
    margin-top: 6rem;
    background-color: #252525;
    margin-bottom: 1rem;
    display: flex;
    padding: 3rem 0;
    justify-content: space-around;
    align-items: flex-start;
    gap: 15px;
    flex-wrap: wrap;
}

/* section one  */
.f1 h2 {
    color: rgb(255, 255, 255);
    font-size: 2.4rem;
    font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
    font-weight: 600;
    text-transform: uppercase;
    letter-spacing: 2px;
}

.f1 p {
    color: rgb(187, 255, 187);
    font-size: 18px;
    letter-spacing: 1px;
    font-weight: 500 !important;
    margin-top: 5px;
}

.f1-img {
    display: flex;
    justify-content: center;
    align-items: center;
    gap: 5px;
}

.f1 ul {
    list-style: none;
    margin-top: 1rem;
}

.f1 ul li a {
    color: gray;
    text-decoration: none;
    font-weight: 400;
    transition: all 0.1s ease;
}

.f1 ul li {
    margin-top: 30px;
    font-style: normal !important;
}

.f1 ul li a:hover {
    color: rgb(110, 211, 250);

}

/* section two  */
.f2 ul {
    list-style: none;
    margin-top: 1rem;
}

.f2 h3 {
    color: #fff;
    text-transform: uppercase;
    border-left: 3px solid rgb(187, 255, 187);
    padding-left: 6px;
    font-weight: 500;
}

.f2 ul li a {
    color: gray;
    line-height: 40px;
    text-decoration: none;
    font-family: sans-serif;
    font-weight: 500;
    transition: all 0.1s ease;
}

.f2 ul li a:hover {
    color: rgb(110, 211, 250);
}


.f2 ul {
    list-style: none;
    margin-top: 1rem;
}

.f2 h3 {
    color: #fff;
    text-transform: uppercase;
    border-left: 3px solid rgb(187, 255, 187);
    padding-left: 6px;
    font-weight: 500;
}

.f2 ul li a {
    color: gray;
    line-height: 40px;
    text-decoration: none;
    font-family: sans-serif;
    font-weight: 500;
    transition: all 0.1s ease;
}

.f2 ul li a:hover {
    color: rgb(110, 211, 250);
}

