/* section four  */

.f4 h3 {
    color: #fff;
    text-transform: uppercase;
    border-left: 3px solid rgb(187, 255, 187);
    padding-left: 6px;
    font-weight: 500;
    margin-bottom: 1rem;
}

.f4 p {
    /* font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif; */
    color: rgb(172, 172, 172);
    font-size: 13px;
    letter-spacing: 1px;
    /* font-weight: 500 !important; */
    margin-top: 5px;
}

form {
    margin-top: 2rem;
}

form input:nth-child(1) {
    width: 13rem;
    height: 3rem;
    background-color: transparent;
    border: 1px solid rgba(188, 188, 188, 0.548);
    border-radius: 10px 0 0 10px;
    border-right: none;
    color: gray;
    padding: 0 1rem;
    font-size: 12px;
}

form input:nth-child(2) {
    width: 7rem;
    height: 3rem;
    background-color: transparent;
    border: 1px solid rgba(188, 188, 188, 0.548);
    border-radius: 0 10px 10px 0;
    border-left: none;
    color: gray;
    padding: 0 1rem;
    font-size: 12px;
}

form input:nth-child(1):focus {
    background-color: #fff;
    outline: none;
    font-size: 15px;
}


@media (max-width:380px) {
    .Bfooter {
        padding: 1rem 1.5rem;
        gap: 20px;
    }

    .f1 h2 {
        font-size: 2rem;
    }

    .f1 ul li a {
        font-size: 14px;
        letter-spacing: 0.8px
    }

    .f1 ul li {
        margin-top: 20px;
    }

    .f2,
    .f3,
    .f4 {
        margin-top: 10px;
    }

    form input:nth-child(1) {
        width: 10rem;
    }
}

@media (max-width:320px) {
    form input:nth-child(1) {
        width: 8rem;
    }

    form input:nth-child(2) {
        width: 5rem;
    }
}

@media (max-width:340px) {
    .Bfooter {
        justify-content: flex-start;
    }
}