* {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
}

.services {
    width: 100%;
    min-height: 100vh;
    margin-top: 4rem;
    font-family: 'Lucida Sans', 'Lucida Sans Regular', 'Lucida Grande', 'Lucida Sans Unicode', Geneva, Verdana, sans-serif;
}

.title3 {
    width: 100%;
    min-height: 10vh;
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
}

.title3 h1 span {
    background-color: rgb(110, 211, 250);
    padding: 5px 10px;
    color: #fff;
}

.title3 h1 {
    margin-bottom: 20px;
}

.title3 p {
    text-align: center;
    font-size: 13px;
}

.services-bg {
    margin-top: 2rem;
    width: 100%;
    min-height: 85vh;
    background-image: linear-gradient(rgba(0, 0, 0, 0.4), rgba(0, 0, 0, 0.8)), url(/public/asset/images/services-bg.jpg);
    background-size: cover;
    background-position: bottom;
    display: flex;
    justify-content: center;
    /* align-items: center; */
    flex-direction: column;
    flex-wrap: wrap;
    

}

/* .services-bg  .owl-item{
    display: flex !important;
    justify-content: center;
    align-items: center;
} */

.title03 {
    width: 100%;
    min-height: 10vh;
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
}

.title03 h1 {
    margin-bottom: 15px;
    color: #fff;
    border-bottom: 2px solid rgb(110, 211, 250);
    font-weight: 400;
}

.title03 p {
    text-align: center;
    color: #fff;
    font-size: 13px;
}

.skill-services {
    min-height: 50vh;
    display: flex;
    justify-content: space-evenly;
    align-items: center;
    /* border: 1px solid #fff; */
    flex-wrap: wrap;
    gap: 20px;
}

.ss1 {
    width: 25rem;
    min-height: 10rem;
    /* background-color: #c93131; */
    display: flex;
    justify-content: center;
    align-items: center;
    padding: 5px;
    gap: 20px;
    
    
}

.ss1 h2 {
    color: #fff;
    font-weight: 600;
    margin-bottom: 20px;
}

.ss1 p {
    color: #fff;
    font-size: 14px;
    font-weight: 300;
}

.ss1-img {
    background-color: rgb(169, 231, 255);
    /* padding: 20px; */
    border-radius: 50%;
    width: 300px;
    height: 100px;
    display: flex;
    justify-content: center;
    align-items: center;
}

.ss1-img img {
    width: 60px !important;
}

/* delivery section  */
.Dlvserv {
    width: 100%;
    min-height: 50vh;
    background-image: url(https://img.freepik.com/free-photo/pastel-blue-vignette-concrete-textured-background_53876-102637.jpg?w=996&t=st=1674189085~exp=1674189685~hmac=4b96cac6a088f4e850f6bf73317f9de41d3fb53a5614815fe724f4800c16305d);
    display: flex;
    justify-content: center;
    align-items: center;
    background-position: center;
}

.Dlvserv .owl-item{
    display: flex;
    justify-content: center;
    align-items: center;
}

.Dlvserv .owl-dot span{
    border-radius: 30%;
    width: 1rem !important;
    height: 0.8rem !important;
    transition: all 0.3s ease !important;
}


.Dlvserv .owl-dot.active span{
    width: 3rem !important;
}


.Sdlv {
    width: 20rem;
    min-height: 10rem;
    color: rgb(0, 0, 0);
    box-sizing: content-box;
    padding: 12px;
    font-family: 'Lucida Sans', 'Lucida Sans Regular', 'Lucida Grande', 'Lucida Sans Unicode', Geneva, Verdana, sans-serif;
    border-top: 2px solid gold;
    background-color: rgb(255, 255, 255);
    box-shadow: -10px 10px 20px rgba(0, 0, 0, 0.3);
    border-radius: 10px;
}

.Sdlv .cloned {
    width: 20rem !important;
}

.Sdlv img {
    width: 70px !important;
    margin: 5px;
}

.owl-stage {
    display: flex !important;
    justify-content: center;
    align-items: center;
}

.Sdlv p {
    letter-spacing: 1px;
    color: rgb(106, 106, 106);
    font-size: 13.5px;
    font-weight: 500;
}

.Sdlv h3 {
    font-size: 21px;
    padding: 5px 0;
    letter-spacing: 1.5px;
}

@media (max-width:1130px) {
    .Dlvserv{
        padding: 0 1rem;
    }
}

@media (max-width:820px) {
    .services-bg{
        padding: 2rem 0;
    }


}
@media (max-width:450px) {
    .ss1{
        width: 22rem;
    }

    .ss1-img img{
        width: 45px !important;
    }

    .ss1 h2{
        font-size: 20px;
    }
}

@media (max-width:370px) {

    .skill-services{
        padding: 0 2rem;
    }
    .ss1{
        flex-wrap: wrap;
        width: initial;
    }

    .ss1-img{
        width: 100px;
        height: 100px;
    }
}
