.login{
    width: 100%;
    min-height: 100vh;
    background-color: rgba(195, 255, 255, 0.5);
    margin-top: 1.5rem;
    display: flex;
    justify-content: center;
    align-items: center;
    padding: 2rem 0;
}


.user{
    min-height: 82vh;
    display: flex;
    justify-content:center ;
    align-items: center;
}

.login-img{
    width: 26rem;
    height: 39rem;
    background-color: rgb(77, 255, 145);
    background-image: url(https://images.unsplash.com/photo-1618220179428-22790b461013?ixlib=rb-4.0.3&ixid=MnwxMjA3fDB8MHxzZWFyY2h8NXx8ZnVybml0dXJlfGVufDB8fDB8fA%3D%3D&auto=format&fit=crop&w=500&q=60);
    background-position: center;
    border-radius: 25px 0 0 25px;
    background-size: fill;
}

.valid{
  padding: 1rem 1rem 2rem 3rem;
  background-color: #ffffff;
  border-radius: 0 25px 25px 0;
  box-sizing: border-box;
  height: 39rem;
  width: 27rem;
 
}

.valid form {
    display: flex;
    justify-content: flex-start;
    align-items: flex-start;
    flex-direction: column;
    gap: 30px;
    line-height: 25px;
    margin: 20px 0 0 0;
}

.valid form input{
    width: 20rem;
    height: 2.5rem;
    border: 1px solid rgb(184, 184, 184);
    border-radius: 5px;
    padding:  0 5px 0 15px; 
    font-size: 13.5px;
}

.valid form input:focus{
    outline: 1px solid rgb(0, 204, 255);
}

#sub{
    width: 10rem !important;
    background-color: rgb(110, 211, 250);
    border: none;
    color: rgb(6, 7, 22);
    font-size: 15px;
    font-weight: 600;
    transition: all 0.2s ease;
    text-align: center;
    padding: 0 !important;
}

#sub:hover{
 background-color: rgb(16, 214, 158) ;
 color: #fff;


}

#tel{
    width: 10rem !important;
    font-size: 13px;
}

#pin{
    width: 8.5rem;
}
.valid form label{
   font-size: 14px;
   font-weight: 500;
}

.num{
    display: flex;
    gap: 10px;
}

.valid form p{
    font-size: 14px;
    color: gray;
    margin: 0;
    margin-top:3px ;
    text-align: left;
    text-decoration: none !important;
}

.valid form .fpass{
    text-decoration: none !important;
}

.valid p{
    font-size: 14px;
    text-align: center;
    color: gray;
    margin-top: 1.5rem;
    /* margin-bottom: 5px; */
}

.valid p span{
    color: blue;
}

.gAct {
    display: flex;
    justify-content: center;
    align-items: flex-end;
    height: 4rem;
    gap: 25px;
    /* margin-top: 10px; */
}

.gAct .S-img{
    padding: 6px;
    width: 35px;
    height: 35px;
    background-color: rgba(197, 197, 197, 0.281);
    box-sizing: content-box;
    border-radius: 50px;
    display: flex;
    justify-content: center;
    align-items: center;

}

.gAct .S-img:hover{
    margin-bottom: 10px !important;
    /* background-color: aqua; */
    transition: all 0.2s ease;

}


#or{
    margin-top: 15px !important;
}

#s-with{
    text-align: center !important;
    font-size: 13px;
    margin-left: 8.5rem;
    color: rgb(0, 164, 197);
    font-weight: 500;
    letter-spacing: 0.5px;
}

.valid h2{
    letter-spacing: 1.5px;
    color: rgb(0, 143, 186);
    font-family: 'Lucida Sans', 'Lucida Sans Regular', 'Lucida Grande', 'Lucida Sans Unicode', Geneva, Verdana, sans-serif;
}

.login button{
    font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
    text-align: center !important;
    color: rgb(5, 0, 157);
    /* margin-left: 11rem; */
    background-color: transparent;
    font-weight: 700 !important;
    border: none;
    font-size: 16px;
    margin-top: 1rem;
}

.login button:hover{
    color: rgb(216, 20, 20);
}

#ls{
    color: gray;
    font-size: 15px;
    margin-right: 10px; 
    margin-left: 70px;
}  

#fpass{
    color: rgb(18, 0, 212);
}

/* responsiveness  */

@media (max-width:950px) {
    .login-img{
        width: 24rem;
    }

    .valid{
        width: 25rem;
    }
}

@media (max-width:890px) {
    .valid h2{
        font-size: 20px;
    }

    .valid form input{
        width: 17rem;
        height: 2.4rem;
    }

    #tel{
        width: 8rem !important;
    }

    #pin{
        width: 7.5rem;
    }
}

@media (max-width:820px) {
    .login{
        position: relative;
        justify-content: left;
        padding:  0 1rem 0 5rem;
    }

    .valid{
        position: sticky;
        z-index: 2;
        border-radius: 25px;
        margin-top: 3rem;
        box-shadow: 10px 10px 15px rgba(0, 0, 0, 0.281);
    }

    .login-img{
        position: absolute;
        height: 30rem;
        border-radius: 0;
        z-index: 1;
        right: 3rem;
        top: 1rem;
    }
}

@media (max-width:630px) {
    .valid{
        width: 22rem;
    }

    .valid form input{
        width: 15rem;
        height: 2.3rem;
    }

    #tel{
        width: 7rem !important;
    }

    #pin{
        width: 7rem;
    }

    #ls{
        font-size: 13px;
        margin-left: 30px;
    }

    #s-with{
        margin-left: 6.5rem;
    }
}

@media (max-width:550px) {
    .login{
        padding:  0 1rem 0 3rem;
    }

    .valid{
        width: 20rem;
        padding: 1rem 1rem 1rem 2rem;
    }

    #sub{
        width: 8rem !important;
        font-size: 14px;
    }

    .login-img{
        width: 19rem;
        height: 28rem;
        right: 2rem;
    } 
}

@media (max-width:450px) {

    .login-img{
       display: none;
    }
    .login{
        /* padding: 1rem !important; */
        background-image: url("https://images.unsplash.com/photo-1618220179428-22790b461013?ixlib=rb-4.0.3&ixid=MnwxMjA3fDB8MHxzZWFyY2h8NXx8ZnVybml0dXJlfGVufDB8fDB8fA%3D%3D&auto=format&fit=crop&w=500&q=60");
    }
}

@media (max-width:400px) {
    .login{
        
            padding:  0 1rem 0 1rem;
        
    }
}