@import url('https://fonts.googleapis.com/css2?family=Pacifico&display=swap');
/*font family*/

.bg-image {
    width: 100%;
    height: 90vh;
    background-image: url(/public/asset/images/home-bg.jpg);
    background-size: cover;
    background-position: bottom;
    display: flex;
    justify-content: center;
    align-items: center;
    padding-bottom: 5rem;
}

.bg-content {
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
    gap: 40px;
}

.bg-content h1 {
    font-size: 2.6rem;
}

.bg-content span {
    background-color: rgb(29, 187, 132);
    padding: 5px 10px;
}


.bg-content button {
    width: 10rem;
    height: 3rem;
    background-color: rgba(80, 201, 248, 0.7) !important;
    border: none;
    font-weight: 700;
    font-size: 14px;
    position: relative;
    background-color: transparent;
    box-shadow: 0 0 10px rgba(0, 0, 0, 0.5);
    /* color: gray; */
}

.bg-content button::after {
    content: "";
    position: absolute;
    width: 10rem;
    height: 2.6rem;
    background-color: transparent;
    border: 2px solid rgb(255, 255, 255);
    left: 10px;
    top: 8px;
    transition: all 0.5s ease;
}

.bg-content button::before {
    content: "";
    position: absolute;
    width: 10rem;
    height: 2.6rem;
    background-color: transparent;
    border: 2px solid rgb(255, 255, 255);
    left: -14px;
    top: -14px;
    transition: all 0.5s ease;
}

.bg-content button:hover::before {
    left: 0;
    top: 0;
}

.bg-content button:hover::after {
    left: 0;
    top: 0;
}

.bg-content button:hover {
    background-color: #fff;
    color: black;
}


@media (max-width:477px) {
    .bg-content h1 {
        font-size: 2.2rem;
    }
}

@media (max-width:400px) {
    .bg-content h1 {
