.addcart {
    min-height: 100vh;
    width: 100%;
    display: flex;
    font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
}

.image {
    background-color: rgba(173, 173, 173, 0.317);
    height: 100vh !important;
    width: 45%;
    display: flex;
    justify-content: center;
    align-items: center;
    padding: 0 2.8rem;
    flex-direction: column;
    gap: 15px;
}

.image .nestcard-img {
    margin: 0 2rem;
    box-sizing: border-box;
    overflow: hidden;
}

.image img {
    border-radius: 10px;
    height: 30rem !important;
    object-fit: cover !important;

}

.image .owl-prev {
    background-color: rgb(138, 220, 255) !important;
    width: 32px;
    height: auto;
    left: 1.7rem;
    border-radius: 0% !important;
    font-size: 32px !important;
    display: flex !important;
    justify-content: center;
    align-items: center;
    box-sizing: border-box;
    text-align: center;
}

.image .owl-next {
    background-color: rgb(138, 220, 255) !important;
    width: 32px;
    height: auto;
    border-radius: 0% !important;
    font-size: 32px !important;
    display: flex !important;
    justify-content: center;
    align-items: center;
    box-sizing: border-box;
    text-align: center;
}


.fpage-content {
    width: 50%;
    height: 130vh;
    padding: 50px 50px;
    box-sizing: border-box;
    /* border: 1px solid rgb(0, 225, 255); */
    line-height: 30px;
    overflow-y: scroll;
    overflow-x: hidden;
    /* background-color: rgb(235, 235, 235); */


}

.fpage-content::-webkit-scrollbar {
    display: none;
}

.fpage-content h2 {
    font-size: 28px;
    font-weight: 600;
}

.fpage-content span {
    color: gray;
    text-transform: capitalize !important;
}

.fpage-content .p1 {
    background-color: rgb(150, 225, 255);
    padding: 0 8px;
    border-radius: 5px;
    display: inline-block;
    margin: 5px 10px 15px 0;
}

.fpage-content .p1 i {
    color: rgb(255, 225, 0);
}

.prc {
    font-size: 25px;
    font-weight: 600;
    display: inline-block;
    margin-right: 10px;
}

#off {
    color: rgb(47, 236, 47);
    font-weight: 500;
}

#soff {
    font-size: 20px;
    color: rgb(51, 180, 231);
    font-weight: 600;
    margin-top: 10px;
}

.fpage-content #result2 #display2 {
    position: fixed;
    background-color: rgb(26, 26, 26);
    color: white;
    width: 20rem;
    height: 3rem;
    display: flex;
    justify-content: center;
    align-items: center;
    left: 40%;
    z-index: 10000;
    animation: success 2s linear;
    gap: 20px;
    border-radius: 5px;
    bottom: -20%;

}

.fpage-content #result2 img {
    width: 20px;
}

@keyframes success {
    0% {
        bottom: -40%;
    }

    50% {
        bottom: 40%;
    }

    100% {
        bottom: 40%;
    }
}

.fpage-content ul li {
    color: rgb(82, 82, 82);
    font-weight: 500;
}

.fpage-content .owl-prev {
    background-color: #fff !important;
}

.fpage-content .inc {
    width: 3rem;
    height: 2.3rem;
    background-color: #fff;
    border: none;
    box-shadow: 5px 5px 10px rgba(0, 0, 0, 0.3);
    font-size: 19px;
    color: rgb(56, 162, 203);
    font-weight: 600;
    margin-top: 15px;

}

#count {
    font-size: 18px;
    font-weight: 600;
    color: black !important;
    padding: 5px 20px;
    /* border: 1px solid gray; */
    height: 50px;
    width: 50px !important;
    background-color: rgba(188, 188, 188, 0.26);

}

.fdlv {
    display: flex;
    /* justify-content: center; */
    align-items: center;
    gap: 10px;
    margin-top: 5px;
}

.fpage-content form {
    display: flex;
}

.fpage-content form label {
    font-size: 17px !important;
    font-weight: 500;
}

.fpage-content form .pcd input:nth-child(1) {
    border: 1px solid rgba(128, 128, 128, 0.42);
    width: 15rem;
    height: 2.5rem;
    margin: 0 10px 0 15px;
    border-radius: 5px 0 0 5px;
    padding: 0 15px;
    font-size: 13px;
}

.fpage-content form .pcd input:nth-child(2) {
    width: 6rem;
    height: 2.5rem;
    border: none;
    background-color: rgb(110, 211, 250);
    border-radius: 0 5px 5px 0;
    font-size: 14px;
    color: #fff;
    font-weight: 700;
    border-right: 3px solid gray;
}

.fpage-content form input:nth-child(3):hover {
    background-color: rgb(13, 161, 202);
}

.add {
    width: 10rem;
    height: 2.8rem;
    text-align: center;
    border: none;
    background-image: linear-gradient(to right, rgb(102, 233, 248), rgb(4, 54, 73));
    color: #fff;
    font-weight: 600;
    margin-top: 1.5rem;
    margin-right: 1.5rem;
    border-radius: 5px;
}

.add:hover {
    background: rgba(0, 0, 0, 0.9);
}

.fpage-content h5 {
    font-size: 20px;
    color: rgb(101, 101, 101);
    font-weight: 600;
    margin-top: 20px;
}

.fpage-content table {
    /* background-color: gray; */
    width: 35rem;
    text-align: left;
    padding: 10px;
}

.fpage-content table tr:nth-child(odd) {
    background-color: aliceblue;
}

.fpage-content table th {
    font-weight: 600;
    padding: 5px 10px;
}

.fpage-content table td {
    padding: 5px 10px;
}

.lik {
    color: rgb(9, 0, 132);
    text-decoration: none;
    font-size: 16px;
    font-weight: 600;
    padding: 0 10px;
}


/* responsiveness Details  */
@media (max-width:1070px) {
    .fpage-content ul li {
        font-size: 15px;
    }

    .fpage-content form .pcd input:nth-child(1) {
        width: 12rem;
    }

    .fpage-content form .pcd input:nth-child(2) {
        width: 5rem;
    }
}

@media (max-width:950px) {
    .fpage-content {
        padding: 50px 15px 50px 50px;
    }

    .image {
        width: 50%;
    }
}

@media (max-width:900px) {
    .addcart {
        flex-direction: column;
    }

    .image {
        width: 100%;
    }

    .image .owl-carousel {
        width: 70% !important;
    }

    .image .owl-item {
        display: flex !important;
        justify-content: center !important;
        align-items: center;
    }

    .image img {
        border-radius: 10px;
        height: 27rem !important;
        object-fit: cover !important;
    }

    .fpage-content {
        width: 100%;
    }

}

@media (max-width:610px) {
    .fpage-content table {
        width: inherit;
    }

    .image .owl-prev,.image .owl-next{
        display: none !important;
    }

    .image .nestcard-img{
        margin: 0;
    }
}






@media (max-width:460px) {
    .fpage-content ul li {
        font-size: 14px;
    }

    .fpage-content form {
        flex-direction: column;
        gap: 5px;
    }

    .fpage-content form label {
        font-size: 15px !important;
    }

    .fpage-content form .pcd input:nth-child(1) {
        margin: 0 10px 0 0;
    }

    /* .image .owl-item{
        margin: 0 0.2rem;
        width: 100% !important;
        display: flex !important;
        justify-content: center;
        align-items: center;
    }
    .image{
        padding: 0;
    } */

   .ad {
    position: fixed;
    bottom: 0;
    left: 0;
    width: 51%;
    z-index: 1000;
    border-radius: 0;
    background-color: #ffffff;
    background-image: none;
    color: black;
    box-shadow: 0 -1px 8px rgba(0, 0, 0, 0.121);
}

.by {
    position: fixed;
    bottom: 0;
    right: -30px;
    width: 51%;
    z-index: 1000;
    border-radius: 0;
    background-color: rgb(24, 221, 235) !important;
    background-image: linear-gradient(to right, rgb(40, 208, 227), rgb(40, 208, 227));
    box-shadow: 0 -1px 8px rgba(0, 0, 0, 0.121);
}

    
}

@media (max-width:380px) {
    .fpage-content{
        padding: 50px 15px 50px 20px;
    }

    .fpage-content ul {
        margin-left: 20px;
    }
}




/* start review section  */
.review {
    width: 100%;
    min-height: 100vh;
    background-color: rgba(247, 251, 255, 0.612);
    display: flex;
    margin-top: 3rem;
}

.star-rev {
    flex-basis: 25%;
    background-color: rgb(255, 255, 255);
    display: flex;
    justify-content: flex-start;
    align-items: center;
    height: 70vh;
    flex-direction: column;
    border-top: 2px solid rgb(0, 217, 255);
    padding: 2rem 0;
    line-height: 25px;

}

.star-rev i {
    color: gold;
    margin-right: 2px;
}

.images-review {
    flex-basis: 70%;
    /* background-color: beige; */
    height: 110vh;
    padding: 1rem 2rem;
    overflow-y: scroll;
}

.images-review::-webkit-scrollbar {
    display: none;
}

.images-review h4 {
    font-size: 19px;
    margin: 15px 0;
}

.images-review img {
    width: 200px;
    height: 200px;
    object-fit: cover;
    border-radius: 5px;
    outline: 4px solid #fff;
}

.slide-img {
    display: flex;
    gap: 20px;
    border-bottom: 1px solid gray;
    padding: 2px 0 15px 0;
    flex-wrap: wrap;
}


.plus {
    position: relative;
}

.plus::before {
    position: absolute;
    content: "+100";
    width: 200px;
    height: 200px;
    background-color: rgba(0, 0, 0);
    /* left: 0;
    top: 0; */
    display: flex;
    justify-content: center;
    align-items: center;
    border-radius: 5px;
}

.all-review {
    margin-top: 10px;
    line-height: 25px;
    background-color: rgb(220, 219, 219, 0.4);
    padding: 0.8rem 2rem;
}

.all-review h5 {
    font-size: 16px;
    font-weight: 600;
    /* color: gray; */
}

.star-rev h3 {
    font-size: 20px;
    color: rgb(110, 211, 250);
}

.progress-star {
    margin-left: 20px;
    margin-top: 10px;
}

.progress-star p {
    color: gray;
}

.progress-star button {
    width: 10rem;
    height: 2.6rem;
    background-color: rgb(37, 37, 37);
    margin-top: 15px;
    color: #fff;
    font-weight: 600;
    font-size: 15px;
    border: none;
}

.progress-star button:hover {
    background-color: rgb(0, 255, 170);
    transition: all 0.3s ease;
    color: black;
}

.all-review i {
    color: rgb(241, 207, 15);
}


.progress-star meter {
    width: 12rem !important;
    height: 1.3rem !important;
    /* background-color: rgb(88, 255, 88) !important; */
    border-radius: 0 !important;
    margin-top: 5px;
}

.progress-star meter::-webkit-meter-optimum-value {
    background-color: rgb(255, 165, 46) !important;
}

.progress-star meter::-webkit-meter-bar {
    border-radius: 5px !important;
    border: none;
    /* box-shadow: 2px 2px 7px rgba(0, 0, 0, 0.3); */
    background-color: #d6d6d6 !important;

}