

.sign-img{
    width: 100%;
    height: 100vh;
    background-color: rgb(49, 159, 255);
    background-image: url(/public/asset/images/svg.png);
    background-position: bottom;
    background-size: 100% 85%;
    background-repeat: no-repeat;
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
    gap: 30px;
}

.signpage{
    min-height: 26rem;
    width: 25rem;
    background-color: rgb(255, 255, 255,0.5);
    border-radius: 15px;
    box-shadow:inset 0 0 10px rgba(0, 0, 0, 0.3), 8px 8px 10px rgba(0, 0, 0, 0.3);
    padding: 3rem 0;
    
}

.signpage h2{
    text-align: center;
    letter-spacing: 1px;
    font-family:  Georgia, 'Times New Roman', Times, serif;
    color: rgb(38, 38, 38);
    font-weight: 600;
    font-size: 28px;
}

.signpage form{
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
    gap: 20px;
    margin-top: 3rem;
}

#L-pass,#L-email{
    width: 20rem;
    height: 3rem;
    background-color: aliceblue;
    border: none;
    border-radius: 15px;
    font-size: 14px;
    font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
    box-shadow: 3px  3px 10px rgba(0, 0, 0, 0.2);
}

#L-pass:focus,#L-email:focus{
    /* outline: none; */
    outline: 1px solid rgb(24, 242, 169);
    background-color: rgb(233, 255, 231) !important;
}

#L-submit{
    width: 10rem;
    height: 2.6em;
    border: none;
    background-color: rgb(32, 239, 180);
    background-image: linear-gradient(to right,rgb(32, 239, 180),rgb(0, 199, 159));
    border-radius: 5px;
    font-size: 15px;
    font-weight: 600;
    color: #fff;
    text-transform: capitalize;
}

#L-submit:hover{
    background-color: black;
    background-image: linear-gradient(rgb(69, 69, 69),black);
}

.L-para{
    text-align: center;
    font-size: 14px;
    margin-bottom: 10px;
    margin-top: 25px;
    font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
    font-weight: 500;
}

.L-sign{
    text-decoration: none;
   
}

.L-sign span{
    font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
    text-align: center !important;
    color: rgb(5, 0, 157);
    margin-left: 11rem;
    font-weight: 700 !important;
}

.L-sign span:hover{
    color: rgb(216, 20, 20);
}


.L-with{
    width: 20rem;
    min-height: 4rem;
    background-color: rgb(234, 234, 234);
    border-radius: 5px;
    display: flex;
    justify-content: space-evenly;
    align-items: center;

}

.L-email:hover,.L-face:hover,.L-glg:hover{
    margin-top: -10px;
    transition: all 0.2s ease;
    /* background-color:  rgba(127, 255, 212, 0.651); */
}

.L-with p{
    font-weight: 700;
    color: rgb(20, 206, 223);
    font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
}
.fortext{
    font-size: 14px;
    margin-top: -8px;
    color: rgb(0, 122, 244);
    text-decoration: none;
    text-align: right !important;
    width: 19rem;
}

/* start forgot password section  */

.forgot{
  min-height: 80vh;
  width: 100%;
  background-image: linear-gradient(to bottom right,rgb(124, 251, 255),rgb(5, 0, 101));
  display: flex;
  justify-content: center;
  align-items: center;
  /* padding: 2rem 0 !important; */
}

.for{
   width: 25rem;
   min-height: 25rem;
   /* background-color: #fff; */
   display: flex;
   justify-content: center;
   align-items: center;
   flex-direction: column;
   gap: 15px;
   
}

#back{
    text-decoration: none;
}

#back span{
    color: #fff;
    font-size: 14px;
    text-decoration: none;
    text-transform: capitalize;
    margin-right: 5px;
    cursor: pointer;
}

#back img{
    width: 25px !important;
    height: auto !important;
    cursor: pointer;
}

.for img{
    border-radius: 50%;
    height: 11rem;
}

.for h2{
    color: #fff;
    font-weight: 500;
    font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
    letter-spacing: 1px;
}

.for p{
    color: #fff;
    font-size: 14.5px;
    text-align: center;
    /* font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif; */
    letter-spacing: 0.5px;
    line-height: 20px;
}

.for form{
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
    gap: 22px;
}


.for form input:nth-child(1){
   border: none ;
   border-bottom: 2px solid rgb(197, 197, 197);
   width: 20rem;
   border-radius: 0;
   font-size: 14px;
}

.for form input:nth-child(1)::placeholder{
    color: #ffffff;
    opacity: 0.7;
}

.for form input:nth-child(1):focus::placeholder{
    color: gray;
}

.for form input:nth-child(2){
    width: 20rem;
    height: 2.5rem;
    border: none;
    background-color: rgb(26, 255, 179);
    border-radius: 20px;
    font-size: 14px;
    color: #fff;
    font-weight: 600;
    cursor: pointer;
}

.for form input:nth-child(2):hover{
    background-color: black;
    transition: all 0.2s ease-out;
}

/* responsive media quaries  */

@media (max-width:490px) {
    .signpage{
        width: 20rem;
    }

    #L-pass,#L-email{
        width: 15rem;
        height: 2.6rem;
    }

    #L-submit{
        height: 2.4rem;
    }

    .fortext{
        font-size: 13px;
        margin-right: 4rem;
    }

    .L-sign span{
        margin-left: 9rem;
        font-size: 14px;
    }

    .L-email img,.L-face img,.L-glg img{
        width: 25px !important;
    }

    .L-email ,.L-face,.L-glg {
        width: 20px;
    }

    .L-with{
        width: 18rem;
    }
}

@media (max-width:350px) {
    .signpage{
        width: 16rem;
    }

    #L-pass,#L-email{
        width: 14rem;
    }

    .signpage h2{
        font-size: 23px;
    }

    #L-submit{
        height: 2.3rem;
        width: 8rem;
        font-size: 14px;
    }

    .fortext{
        margin-right: 5rem;
    }

    .L-para{
        font-size: 13px;
    }
    .L-sign span{
        margin-left: 6.5rem;
        font-size: 13px;
    }
}


/* forgot password media quirys */
@media (max-width:480px) {
   .for img{
    height: 9rem;
} 

.for p{
font-size: 13px;
}

.forgot{
    padding: 0 1.5rem;
}

.for form input:nth-child(2){
    width: 15rem;
    margin-top: 10px;
}

.for form input:nth-child(1){
    width: 18rem;
}
}
