

.contact{
    width: 100%;
    min-height: 140vh;
    position: relative;
    /* background-color: aquamarine; */
}

.C-bg{
    width: 100%;
    height: 50vh;
    background-color: rgb(221, 221, 221);
}

.C-img{
    position: absolute;
    top: 15%;
    left: 35%;
    z-index: 5;
    width: 55%;
    height: 55%;
    background-color: rgb(0, 205, 136) !important;
    background-image: url("https://images01.nicepage.com/c461c07a441a5d220e8feb1a/7e8527524b5058aba0934eab/-min.jpg");
    background-size: cover;
}

.C-img img{
    width: 100%;
    position: absolute;
}

.C-lines{
    position: absolute;
    top: 30%;
    left: 30%;
    z-index: 4;
    width: 55%;
    height: 48%;
    /* background-color: aqua; */
    background-image: url("https://images01.nicepage.com/aa/49/aa49e5bbcb321c65e1d4ba4d4afa599b.png");
    background-size: cover;
}

/* .C-lines img{
    width: 100%;
    height: 100%;
    position: absolute;
} */

.Contact-card{
    position: absolute;
    width: 37rem;
    min-height: 40rem;
    background-color: #fff;
    z-index: 10;
    left: 11%;
    top: 25%;
    box-shadow: 0 0 12px rgba(0, 0, 0, 0.372);
    border-radius: 15px;
    /* display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column; */
    padding: 4rem 0;
}

.Contact-card h1{
    text-align: center;
}



.C-form{
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  gap: 21px;
  
}

.C-form #Cemail,#Cname{
    width: 26rem !important;
    height: 4rem !important;
    border: none !important;
    border-bottom: 3px solid black !important;
    border-radius: 0 !important;
    font-size: 17px;
    font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif !important;
}

.C-form textarea{
    min-width: 26rem !important;
    min-height: 4rem !important;
    border: none !important;
    border-bottom: 3px solid black !important;
    border-radius: 0 !important;
    font-size: 17px;
    font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif !important;
    padding: 0 15px;
    margin-top: 10px;
}

.C-sub{
    margin-top: 2rem;
    height: 2.8rem !important;
    border: none !important;
    border-bottom: none !important;
    background-color: rgb(20, 203, 142);
    border-radius: 20px;
    font-weight: 700;
    font-size: 17px;
    color: #fff;
    width: 26rem;
    font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif !important;
    cursor: pointer;
}

.C-sub:hover{
    background-color: black;
}

.Contact-card h1{
    font-weight: 500;
    font-size: 33px;
    letter-spacing: 1px;
    font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif !important;

}


.C-info{
    position: absolute;
    width: 60rem;
    height: 25vh;
    background-color: rgba(20, 203, 142);
    z-index: 15;
    bottom: 0rem;
    left: 22%;
    display: flex;
    justify-content: space-evenly;
    align-items: center;
    text-align: center;
    border-radius: 5px;
}

.C-info .C-call img,.C-locat img,.C-eml img{
   width: 30px;
   margin-bottom: 20px;
}
.C-info .C-call p ,.C-locat p,.C-eml p{
   color: #fff;
}

/* responsiveness  */


@media (max-width:1245px) {
